export enum ComunitatAutnoma {
    Espanya,
    Andalusia,
    Arag,
    Astries,
    Balears,
    Canaries,
    Cantbria,
    CastellaILle,
    CastellaLaManxa,
    Catalunya,
    Extremadura,
    Galcia,
    LaRioja,
    Madrid,
    Mrcia,
    PasValenci,
}

export enum GrauDiscapacitat {
    Cap,
    Menor33,
    De33A65,
    Major65,
}

export interface Dependent {
    edat: number;
    discapacitat: GrauDiscapacitat;
    assistncia: boolean;
}

export interface DadesComunitatAutnoma {
    getMnimContribuent(edat: number): number;
    getMnimDescendent(exclusiva: boolean, edat: number, discapacitat: GrauDiscapacitat, index: number): number;
    getMnimAscendent(edat: number, discapacitat: GrauDiscapacitat): number;
    getMnimDiscapacitat(dependent: Dependent): number;
    getGravamen(baseLiquidable: number): number;
}

interface Tram {
    mnim: number;
    mxim: number;
    tipus: number;
}

class DadesEspanya implements DadesComunitatAutnoma {

    // Protected methods overriden by the subclasses

    protected getDadesMnimContribuent(): {base: number, major65: number, major75: number} {
        return {base: 5550, major65: 6700, major75: 8100};
    }

    protected getDadesMnimDescendent(): {menor3: number, descendents: number[]} {
        return {menor3: 2800, descendents: [2400, 2700, 4000, 4500]};
    }

    protected getDadesMnimAscendent(): {major65: number, major75: number} {
        return {major65: 1150, major75: 2550};
    }

    protected getDadesMnimDiscapacitat(): {major33: number, major65: number, assistncia: number} {
        return {major33: 3000, major65: 9000, assistncia: 3000};
    }

    protected getTrams(): Tram[] {
        // https://sede.agenciatributaria.gob.es/Sede/ca_es/ayuda/manuales-videos-folletos/manuales-ayuda-presentacion/irpf-2022/8-cumplimentacion-irpf/8_4-cuota-integra/8_4_3-gravamen-base-liquidable-general/8_4_3_1-cuota-integra-estatal.html
        return [
            {mnim: 0,          mxim: 12450.00,    tipus: 9.50},
            {mnim: 12450.00,   mxim: 20200.00,    tipus: 12.00},
            {mnim: 20200.00,   mxim: 35200.00,    tipus: 15.00},
            {mnim: 35200.00,   mxim: 60000.00,    tipus: 18.50},
            {mnim: 60000.00,   mxim: 300000.00,   tipus: 22.50},
            {mnim: 300000.00,  mxim: Infinity,    tipus: 24.50}
        ];
    }

    // Public methods

    getMnimContribuent(edat: number): number {
        // https://sede.agenciatributaria.gob.es/Sede/ca_es/ayuda/manuales-videos-folletos/manuales-ayuda-presentacion/irpf-2022/8-cumplimentacion-irpf/8_3-adecuacion-impuesto-circunstancias-personales-familiares/8_3_2-minimo-contribuyente.html
        const dadesMnimContribuent = this.getDadesMnimContribuent();
        return edat >= 75 ? dadesMnimContribuent.major75 : (edat >= 65 ? dadesMnimContribuent.major65 : dadesMnimContribuent.base);
    }

    getMnimDescendent(exclusiva: boolean, edat: number, discapacitat: GrauDiscapacitat, index: number): number {
        // https://sede.agenciatributaria.gob.es/Sede/ca_es/ayuda/manuales-videos-folletos/manuales-ayuda-presentacion/irpf-2022/8-cumplimentacion-irpf/8_3-adecuacion-impuesto-circunstancias-personales-familiares/8_3_3-minimo-descendiente.html
        if (edat >= 25 && discapacitat === GrauDiscapacitat.Cap) {
            return 0;
        }
        const coeficient = exclusiva ? 1 : 0.5;
        const dadesMnimDescendent = this.getDadesMnimDescendent();
        const mnimDescendent = dadesMnimDescendent.descendents[Math.min(index, dadesMnimDescendent.descendents.length - 1)];
        const mnimMenor3 = (edat < 3 ? dadesMnimDescendent.menor3 : 0);
        return (mnimDescendent + mnimMenor3) * coeficient;
    }

    getMnimAscendent(edat: number, discapacitat: GrauDiscapacitat): number {
        // https://sede.agenciatributaria.gob.es/Sede/ca_es/ayuda/manuales-videos-folletos/manuales-ayuda-presentacion/irpf-2022/8-cumplimentacion-irpf/8_3-adecuacion-impuesto-circunstancias-personales-familiares/8_3_4-minimo-ascendiente.html
        const dadesMnimAscendent = this.getDadesMnimAscendent();
        if (edat >= 75) {
            return dadesMnimAscendent.major75;
        }
        if (edat >= 65 || discapacitat !== GrauDiscapacitat.Cap) {
            return dadesMnimAscendent.major65;
        }
        return 0;
    }

    getMnimDiscapacitat(dependent: Dependent): number {
        // https://sede.agenciatributaria.gob.es/Sede/ca_es/ayuda/manuales-videos-folletos/manuales-ayuda-presentacion/irpf-2022/8-cumplimentacion-irpf/8_3-adecuacion-impuesto-circunstancias-personales-familiares/8_3_5-minimo-discapacidad.html
        const dadesMnimDiscapacitat = this.getDadesMnimDiscapacitat();
        switch (dependent.discapacitat) {
            case GrauDiscapacitat.Cap:
            case GrauDiscapacitat.Menor33:
                return 0;
            case GrauDiscapacitat.De33A65:
                return dadesMnimDiscapacitat.major33 + (dependent.assistncia ? dadesMnimDiscapacitat.assistncia : 0);
            case GrauDiscapacitat.Major65:
                return dadesMnimDiscapacitat.major65 + (dependent.assistncia ? dadesMnimDiscapacitat.assistncia : 0);
        }
    }

    getGravamen(baseLiquidable: number): number {
        const trams = this.getTrams();

        let quota: number = 0;
        for (const tram of trams) {
            if (baseLiquidable >= tram.mnim) {
                quota += (Math.min(baseLiquidable, tram.mxim) - tram.mnim) * (tram.tipus / 100.);
            } else {
                break;
            }
        }
        return quota;
    }
}

