export function getDadesComunitatAutnoma(comunitatAutnoma: ComunitatAutnoma): DadesComunitatAutnoma {
    switch (comunitatAutnoma) {
        case ComunitatAutnoma.Espanya:
            return new DadesEspanya();
        case ComunitatAutnoma.Andalusia:
            return new DadesAndalusia();
        case ComunitatAutnoma.Arag:
            return new DadesArag();
        case ComunitatAutnoma.Astries:
            return new DadesAstries();
        case ComunitatAutnoma.Balears:
            return new DadesBalears();
        case ComunitatAutnoma.Canaries:
            return new DadesCanaries();
        case ComunitatAutnoma.Cantbria:
            return new DadesCantbria();
        case ComunitatAutnoma.CastellaILle:
            return new DadesCastellaILle();
        case ComunitatAutnoma.CastellaLaManxa:
            return new DadesCastellaLaManxa();
        case ComunitatAutnoma.Catalunya:
            return new DadesCatalunya();
        case ComunitatAutnoma.Extremadura:
            return new DadesExtremadura();
        case ComunitatAutnoma.Galcia:
            return new DadesGalcia();
        case ComunitatAutnoma.LaRioja:
            return new DadesLaRioja();
        case ComunitatAutnoma.Madrid:
            return new DadesMadrid();
        case ComunitatAutnoma.Mrcia:
            return new DadesMrcia();
        case ComunitatAutnoma.PasValenci:
            return new DadesPasValenci();
    }
}
