import Sidebar from './Sidebar'
import { Sankey, SankeyData } from './Sankey';
import { IrpfParameters, calculateIrpf } from './Irpf';

import React from 'react';

import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

enum IrpfNodes {
    RendimentsDelTreball,
    DeducciRendimentsTreball,
    DeducciCotitzaciSS,
    DeducciMovilitatGeogrfica,
    DeducciDiscapacitatContribuent,
    DespesesDedubles,
    Deduccions,
    BaseImposable,
    BaseLiquidable,
    QuotaEstatal,
    QuotaEstatalMnimPersonal,
    QuotaAutonmica,
    QuotaAutonmicaMnimPersonal,
    Retencions,
    SalariNet,
}

function getIrpfNodeName(node: IrpfNodes): string {
    switch (node) {
        case IrpfNodes.RendimentsDelTreball:
            return "Rendiments del treball";
        case IrpfNodes.DeducciRendimentsTreball:
            return "Rendiments del treball";
        case IrpfNodes.DeducciCotitzaciSS:
            return "Cotitzaci a la Seguretat Social";
        case IrpfNodes.DeducciMovilitatGeogrfica:
            return "Movilitat geogrfica";
        case IrpfNodes.DeducciDiscapacitatContribuent:
            return "Discapacitat";
        case IrpfNodes.DespesesDedubles:
            return "Despeses dedubles";
        case IrpfNodes.Deduccions:
            return "";
        case IrpfNodes.BaseImposable:
            return "Base imposable";
        case IrpfNodes.BaseLiquidable:
            return "Base liquidable";
        case IrpfNodes.QuotaEstatal:
            return "Quota estatal";
        case IrpfNodes.QuotaEstatalMnimPersonal:
            return "Quota estatal del mnim personal";
        case IrpfNodes.QuotaAutonmica:
            return "Quota autonmica";
        case IrpfNodes.QuotaAutonmicaMnimPersonal:
            return "Quota autonmica del mnim personal";
        case IrpfNodes.Retencions:
            return "Retencions";
        case IrpfNodes.SalariNet:
            return "Salari net";
    }
}

class App extends React.Component {
    state: { data: SankeyData } = {
        data: {
        nodes: [],
        links: [],
    }};

    inputEvent = (params: IrpfParameters) => {
        console.log(params);

        const irpf = calculateIrpf(params);
        console.log(irpf);

        let links = new Array<{source: IrpfNodes, target: IrpfNodes, value: number}>();

        links.push({
            source: IrpfNodes.RendimentsDelTreball,
            target: IrpfNodes.BaseImposable,
            value: irpf.baseImposable,
        });

        const SumaDespesesDedubles = Object.values(irpf.despesesDedubles).reduce((sum, value) => (sum + value));

        links.push({
            source: IrpfNodes.RendimentsDelTreball,
            target: IrpfNodes.DespesesDedubles,
            value: SumaDespesesDedubles,
        });

        links.push({
            source: IrpfNodes.DespesesDedubles,
            target: IrpfNodes.DeducciRendimentsTreball,
            value: irpf.despesesDedubles.rendimentsDelTreball,
        });

        links.push({
            source: IrpfNodes.DespesesDedubles,
            target: IrpfNodes.DeducciCotitzaciSS,
            value: irpf.despesesDedubles.cotitzaciSS,
        });

        links.push({
            source: IrpfNodes.DeducciRendimentsTreball,
            target: IrpfNodes.Deduccions,
            value: irpf.despesesDedubles.rendimentsDelTreball,
        });

        if (irpf.despesesDedubles.movilitatGeogrfica) {
            links.push({
                source: IrpfNodes.DespesesDedubles,
                target: IrpfNodes.DeducciMovilitatGeogrfica,
                value: irpf.despesesDedubles.movilitatGeogrfica,
            });

            links.push({
                source: IrpfNodes.DeducciMovilitatGeogrfica,
                target: IrpfNodes.Deduccions,
                value: irpf.despesesDedubles.movilitatGeogrfica,
            });
        }

        if (irpf.despesesDedubles.discapacitatContribuent) {
            links.push({
                source: IrpfNodes.DespesesDedubles,
                target: IrpfNodes.DeducciDiscapacitatContribuent,
                value: irpf.despesesDedubles.discapacitatContribuent,
            });

            links.push({
                source: IrpfNodes.DeducciDiscapacitatContribuent,
                target: IrpfNodes.Deduccions,
                value: irpf.despesesDedubles.discapacitatContribuent,
            });
        }

        const QuotaEstatalntegra = irpf.quotaEstatal - irpf.mnimPersonalEstatal;
        const QuotaAutonmicantegra = irpf.quotaAutonmica - irpf.mnimPersonalAutonmic;
        const Retencions = QuotaEstatalntegra + QuotaAutonmicantegra;
        const BaseLiquidableNeta = irpf.baseLiquidable - Retencions;

        links.push({
            source: IrpfNodes.Deduccions,
            target: IrpfNodes.SalariNet,
            value: Math.min(SumaDespesesDedubles - irpf.despesesDedubles.cotitzaciSS, irpf.salariNet - BaseLiquidableNeta),
        });

        links.push({
            source: IrpfNodes.BaseImposable,
            target: IrpfNodes.BaseLiquidable,
            value: irpf.baseLiquidable,
        });

        links.push({
            source: IrpfNodes.BaseLiquidable,
            target: IrpfNodes.SalariNet,
            value: BaseLiquidableNeta,
        });

        links.push({
            source: IrpfNodes.BaseLiquidable,
            target: IrpfNodes.QuotaEstatal,
            value: QuotaEstatalntegra,
        });

        links.push({
            source: IrpfNodes.BaseLiquidable,
            target: IrpfNodes.QuotaAutonmica,
            value: QuotaAutonmicantegra,
        });

        links.push({
            source: IrpfNodes.QuotaEstatal,
            target: IrpfNodes.Retencions,
            value: QuotaEstatalntegra,
        });

        links.push({
            source: IrpfNodes.QuotaAutonmica,
            target: IrpfNodes.Retencions,
            value: QuotaAutonmicantegra,
        });

        links.push({
            source: IrpfNodes.QuotaEstatal,
            target: IrpfNodes.QuotaEstatalMnimPersonal,
            value: irpf.mnimPersonalEstatal,
        });

        links.push({
            source: IrpfNodes.QuotaAutonmica,
            target: IrpfNodes.QuotaAutonmicaMnimPersonal,
            value: irpf.mnimPersonalAutonmic,
        });

        // Generate final data object

        let nodeSet = new Set<IrpfNodes>();

        for (const link of links) {
            nodeSet.add(link.source);
            nodeSet.add(link.target);
        }

        const nodeList = Array.from(nodeSet);
        let nodeMap = new Map<IrpfNodes, number>();

        nodeList.forEach((node, index) => {
            nodeMap.set(node, index);
        });

        let data: SankeyData = {
            nodes: nodeList.map((node) => ({
                name: getIrpfNodeName(node)
            })),
            links: links.map((link) => ({
                source: nodeMap.get(link.source) ?? 0,
                target: nodeMap.get(link.target) ?? 0,
                value: link.value,
            })),
        };

        this.setState({data});
    };

    renderSankey(): JSX.Element | null {
        if (this.state.data.nodes.length > 0) {
            return (
                <Sankey width={600} height={600} data={this.state.data} />
            );
        }
        return null;
    }

  render() {
        return (
        <Container fluid className='h-100'>
            <Row className='h-100'>
            <Sidebar onInput={this.inputEvent} />

            <Col fluid className="p-5">
                { this.renderSankey() }
            </Col>
            </Row>
        </Container>
        );
    }
}

export default App;
