import { ComunitatAutnoma, getDadesComunitatAutnoma, Dependent, GrauDiscapacitat, DadesComunitatAutnoma } from "./Dades";
import { formatCurrency } from "./Currency";

export { ComunitatAutnoma, GrauDiscapacitat } from "./Dades";
export type { Dependent } from "./Dades";

export enum CategoriaProfessional {
    EnginyersILlicenciats,
    EnginyersTecnicsPritsIAjudantsTitulats,
    CapsAdministratiusIDeTaller,
    AjudantsNoTitulats,
    OficialsAdministratius,
    Subalterns,
    AuxiliarsAdministratius,
    OficialsDePrimeraISegona,
    OficialsDeTerceraIEspecialistes,
    Peons,
    TreballadorsMenors18Anys,
}

export interface IrpfParameters {
    salariBrut: number;
    categoriaProfessional: CategoriaProfessional;
    comunitatAutnoma: ComunitatAutnoma;
    movilitatGeogrfica: boolean;
    contribuent: Dependent;
    descendentsExclusiva: boolean;
    descendents: Dependent[];
    ascendents: Dependent[];
}

type DespesesDedubles = {
    rendimentsDelTreball: number;
    cotitzaciSS: number;
    discapacitatContribuent?: number;
    movilitatGeogrfica?: number;
};

export interface IrpfResult {
    valid: boolean;
    salariBrut: number;
    despesesDedubles: DespesesDedubles,
    baseImposable: number;
    baseLiquidable: number;
    mnimPersonalEstatal: number;
    mnimPersonalAutonmic: number;
    quotaEstatal: number;
    quotaAutonmica: number;
    // Resultat
    tipusRetenci: number;
    salariNet: number;
}

export const SmiAnual = 15120;

function lmitsCotitzaciPerCategoriaProfessional(categoriaProfessional: CategoriaProfessional): [min: number, max: number] {
    switch (categoriaProfessional) {
        case CategoriaProfessional.EnginyersILlicenciats:
            return [1629.30, 4139.40];
        case CategoriaProfessional.EnginyersTecnicsPritsIAjudantsTitulats:
            return [1351.20, 4070.10];
        case CategoriaProfessional.CapsAdministratiusIDeTaller:
            return [1175.40, 4139.40];
        case CategoriaProfessional.AjudantsNoTitulats:
            return [1166.70, 4139.40];
        case CategoriaProfessional.OficialsAdministratius:
            return [1166.70, 4139.40];
        case CategoriaProfessional.Subalterns:
            return [1166.70, 4139.40];
        case CategoriaProfessional.AuxiliarsAdministratius:
            return [1166.70, 4139.40];
        case CategoriaProfessional.OficialsDePrimeraISegona:
            return [1166.70, 4139.40];
        case CategoriaProfessional.OficialsDeTerceraIEspecialistes:
            return [1166.70, 4139.40];
        case CategoriaProfessional.Peons:
            return [1166.70, 4139.40];
        case CategoriaProfessional.TreballadorsMenors18Anys:
            return [1166.70, 4139.40];
    }
}

const PercentatgeCotitzaciContingnciesComunes = 0.047;
const PercentatgeCotitzaciFormaci = 0.001;
const PercentatgeCotitzaciAtur = 0.0155;
const PercentatgeCotitzaciSS = PercentatgeCotitzaciContingnciesComunes + PercentatgeCotitzaciFormaci + PercentatgeCotitzaciAtur;

function getReducciRendimentTreball(RendimentBrut: number): number {
    // https://sede.agenciatributaria.gob.es/Sede/ca_es/ayuda/manuales-videos-folletos/manuales-ayuda-presentacion/irpf-2022/7-cumplimentacion-irpf/7_1-rendimientos-trabajo-personal/7_1_6-reduccion-obtencion-rendimientos-trabajo.html
    if (RendimentBrut <= 13115) {
        return 5565;
    } else if (RendimentBrut < 16825) {
        return 5565 - (RendimentBrut - 13115) * 1.5;
    }
    return 0;
}

function getReducciRendimentDiscapacitat(contribuent: Dependent) {
    // https://sede.agenciatributaria.gob.es/Sede/ca_es/ayuda/manuales-videos-folletos/manuales-ayuda-presentacion/irpf-2022/7-cumplimentacion-irpf/7_1-rendimientos-trabajo-personal/7_1_5-rendimiento-neto-trabajo-gastos-deducibles.html
    if (contribuent.assistncia) {
        return 7750;
    }

    switch (contribuent.discapacitat) {
        case GrauDiscapacitat.Cap:
        case GrauDiscapacitat.Menor33:
            return 0;
        case GrauDiscapacitat.De33A65:
            return 3500;
        case GrauDiscapacitat.Major65:
            return 7750;
    }
}

function getMnimPersonal(
    dades: DadesComunitatAutnoma,
    contribuent: Dependent,
    descendentsExclusiva: boolean,
    descendents: Dependent[],
    ascendents: Dependent[]
): number {
    // https://sede.agenciatributaria.gob.es/Sede/ca_es/ayuda/manuales-videos-folletos/manuales-ayuda-presentacion/irpf-2022/8-cumplimentacion-irpf/8_3-adecuacion-impuesto-circunstancias-personales-familiares/8_3_5-minimo-discapacidad.html
    const suma = (total: number, valor: number) => { return total + valor; };

    const sumaDiscapacitat = (dependents: Dependent[]) => {
        return dependents.map((value: Dependent, index: number, array: Dependent[]) => { return dades.getMnimDiscapacitat(value); }).reduce(suma, 0);
    }

    const mnimPersonal = dades.getMnimContribuent(contribuent.edat);
    const mnimDiscapacitat = dades.getMnimDiscapacitat(contribuent) + sumaDiscapacitat(descendents) + sumaDiscapacitat(ascendents);
    const mnimDescendents = descendents.map((descendent, index) => dades.getMnimDescendent(descendentsExclusiva, descendent.edat, descendent.discapacitat, index)).reduce(suma, 0);
    const mnimAscendents = ascendents.map((ascendent) => dades.getMnimAscendent(ascendent.edat, ascendent.discapacitat)).reduce(suma, 0);

    return mnimPersonal + mnimDiscapacitat + mnimDescendents + mnimAscendents;
}

function getDespesesDedubles(
    rendimentDelTreball: number,
    cotitzaciSSAnual: number,
    contribuent: Dependent,
    movilitatGeogrfica: boolean,
): DespesesDedubles {
    // https://sede.agenciatributaria.gob.es/Sede/ca_es/ayuda/manuales-videos-folletos/manuales-ayuda-presentacion/irpf-2022/7-cumplimentacion-irpf/7_1-rendimientos-trabajo-personal/7_1_5-rendimiento-neto-trabajo-gastos-deducibles.html
    const ReducciRendimentMovilitatGeogrfica = movilitatGeogrfica ? 2000 : 0;
    return {rendimentsDelTreball: 2000 + getReducciRendimentTreball(rendimentDelTreball),
        cotitzaciSS: cotitzaciSSAnual,
        movilitatGeogrfica: ReducciRendimentMovilitatGeogrfica,
        discapacitatContribuent: getReducciRendimentDiscapacitat(contribuent),
    };
}

export function calculateIrpf({
    salariBrut,
    categoriaProfessional,
    comunitatAutnoma,
    movilitatGeogrfica,
    contribuent,
    descendentsExclusiva,
    descendents,
    ascendents,
}: IrpfParameters): IrpfResult {
    const dadesEstatals = getDadesComunitatAutnoma(ComunitatAutnoma.Espanya);
    const dadesAutonmiques = getDadesComunitatAutnoma(comunitatAutnoma);

    console.log("Salari brut: " + formatCurrency(salariBrut));

    const RendimentDelTreball = salariBrut; // + altres rendiments del treball personal
    console.log("Rendiments del treball: " + formatCurrency(RendimentDelTreball));

    const salariBrutMensual = salariBrut / 12;

    // Seguretat Social
    const [minCotitzaciSS, maxCotitzaciSS] = lmitsCotitzaciPerCategoriaProfessional(categoriaProfessional);
    const CotitzaciSSMensual = Math.min(Math.max(salariBrutMensual, minCotitzaciSS), maxCotitzaciSS) * PercentatgeCotitzaciSS;
    const CotitzaciSSAnual = CotitzaciSSMensual * 12;

    const DespesesDedubles = getDespesesDedubles(RendimentDelTreball, CotitzaciSSAnual, contribuent, movilitatGeogrfica);
    const SumaDespesesDedubles = Object.values(DespesesDedubles).reduce((accumulator, value) => accumulator + value);

    const RendimentNetDelTreball = Math.max(RendimentDelTreball - SumaDespesesDedubles, 0);
    const BaseImposable = RendimentNetDelTreball; // + rendiment net de l'estalvi
    const BaseLiquidable = BaseImposable; // - reduccions (declaraci conjunta, etc.)

    console.log("Base imposable: " + formatCurrency(BaseImposable));
    console.log("Base liquidable: " + formatCurrency(BaseLiquidable));

    // Quota ntegra estatal
    // https://sede.agenciatributaria.gob.es/Sede/ca_es/ayuda/manuales-videos-folletos/manuales-ayuda-presentacion/irpf-2022/8-cumplimentacion-irpf/8_4-cuota-integra/8_4_3-gravamen-base-liquidable-general/8_4_3_1-cuota-integra-estatal.html
    const MnimPersonalEstatal = getMnimPersonal(dadesEstatals, contribuent, descendentsExclusiva, descendents, ascendents);
    const QuotaBaseLiquidableEstatal = dadesEstatals.getGravamen(BaseLiquidable);
    const QuotaMnimPersonalEstatal = dadesEstatals.getGravamen(MnimPersonalEstatal);
    const QuotantegraEstatal = Math.max(QuotaBaseLiquidableEstatal - QuotaMnimPersonalEstatal, 0);
    console.log("Mnim personal estatal: " + formatCurrency(MnimPersonalEstatal));
    console.log("Quota estatal de la base liquidable: " + formatCurrency(QuotaBaseLiquidableEstatal));
    console.log("Quota estatal del mnim personal i familiar: " + formatCurrency(QuotaMnimPersonalEstatal));
    console.log("Quota ntegra estatal: " + formatCurrency(QuotantegraEstatal));

    // Quota ntegra autonmica
    // https://sede.agenciatributaria.gob.es/Sede/ca_es/ayuda/manuales-videos-folletos/manuales-ayuda-presentacion/irpf-2022/8-cumplimentacion-irpf/8_4-cuota-integra/8_4_3-gravamen-base-liquidable-general/8_4_3_2-cuota-integra-autonomica.html
    const MnimPersonalAutonmic = getMnimPersonal(dadesAutonmiques, contribuent, descendentsExclusiva, descendents, ascendents);
    const QuotaBaseLiquidableAutonmica = dadesAutonmiques.getGravamen(BaseLiquidable);
    const QuotaMnimPersonalAutonmica = dadesAutonmiques.getGravamen(MnimPersonalAutonmic);
    const QuotantegraAutonmica = Math.max(QuotaBaseLiquidableAutonmica - QuotaMnimPersonalAutonmica, 0);
    console.log("Mnim personal autonmic: " + formatCurrency(MnimPersonalAutonmic));
    console.log("Quota autonmica de la base liquidable: " + formatCurrency(QuotaBaseLiquidableAutonmica));
    console.log("Quota autonmica del mnim personal i familiar: " + formatCurrency(QuotaMnimPersonalAutonmica));
    console.log("Quota ntegra autonmica: " + formatCurrency(QuotantegraAutonmica));

    const Quotantegra = QuotantegraEstatal + QuotantegraAutonmica;
    console.log("Quota ntegra: " + formatCurrency(Quotantegra));

    const TipusRetenci = Quotantegra / salariBrut;
    const SalariNet = RendimentDelTreball - CotitzaciSSAnual - Quotantegra;

    console.log("Tipus de retenci: " + formatCurrency(TipusRetenci));
    console.log("Salari net: " + formatCurrency(SalariNet));

    return {
        valid: true,
        salariBrut: salariBrut,
        despesesDedubles: DespesesDedubles,
        baseImposable: BaseImposable,
        baseLiquidable: BaseLiquidable,
        mnimPersonalEstatal: QuotaMnimPersonalEstatal,
        mnimPersonalAutonmic: QuotaMnimPersonalAutonmica,
        quotaEstatal: QuotaBaseLiquidableEstatal,
        quotaAutonmica: QuotaBaseLiquidableAutonmica,
        tipusRetenci: TipusRetenci,
        salariNet: SalariNet
    };
}
